window.addEventListener('load', () => {
  const html = document.documentElement;
  const filtersSectionBtns = document.querySelectorAll('.filters__section-title');
  const modClass = 'is-hide';
  let resizeId = null;

  const Breakpoint = {
    MD: 1079,
    SM: 767,
    XS: 600
  };

  if (filtersSectionBtns.length) {
    const filtersSections = document.querySelectorAll('.filters__section');
    setBorder();

    filtersSectionBtns.forEach(it => {
      addHandler(it);
    });

    window.addEventListener('resize', () => {
      setBorder();

      clearTimeout(resizeId);
      resizeId = setTimeout(() => {
        filtersSectionBtns.forEach(it => {
          addHandler(it);
        });
      }, 150);
    });
    
    function addHandler(it) {
          
      const parent = it.parentElement;
      const next = it.nextElementSibling;

      parent.removeAttribute('style');
      next.removeAttribute('style');
      parent.classList.remove(modClass);

      const nextHeight = next.offsetHeight;

      it.onclick = function () {
        if (parent.classList.contains(modClass)) {
          showFiltersSection(next, nextHeight, parent);
        } else {
          hideFiltersSection(next, parent);
        }
      };
    }

    function hideFiltersSection(next, parent) {
      gsap.to(next, {'height': 0, 'padding-top': 0, 'padding-bottom': 0, duration: .3});
      gsap.to(parent, {'padding-bottom': '20px', duration: .3});

      parent.classList.add(modClass);
    }

    function showFiltersSection(next, nextHeight, parent) {
      gsap.to(next, {'height': nextHeight, 'padding-top': '20px', 'padding-bottom': '2px', duration: .3})
      gsap.to(parent, {'padding-bottom': '23px', duration: .3});

      parent.classList.remove(modClass);
    }

    function setBorder() {
      if (html.clientWidth <= Breakpoint.MD && html.clientWidth > Breakpoint.SM) {
        setSectionBorder(3);
      } else if (html.clientWidth <= Breakpoint.SM && html.clientWidth > Breakpoint.XS) {
        setSectionBorder(2);
      } else if (html.clientWidth <= Breakpoint.XS) {
        setSectionBorder(1);
      } else {
        setSectionBorder(0);
      }
    }

    function setSectionBorder(x) {
      const remainder = filtersSections.length % x || x;
      const validIndex = filtersSections.length - remainder;

      filtersSections.forEach((it, index) => {
        if (index >= validIndex) {
          it.classList.add('no-border');
        } else {
          it.classList.remove('no-border');
        }
      });
      
      
      
    }
  }
});
window.addEventListener('load', () => {
  const html = document.documentElement;
  const submenuLists = document.querySelectorAll('.menu__submenu-list');
  const breakpoint = 1280;
  const secondBreakpoint = 1600;
  const header = document.querySelector('.header');
  let isSubmenuSimplebar = false;

  if (header) {
    const canvas = header.querySelector('.header__canvas');
    const profile = header.querySelector('.header__profile');
    const openBtn = header.querySelector('.header__burger');
    const closeBtn = canvas.querySelector('.header__canvas-close');
    const secondMenu = header.querySelector('.header__second-menu');
    const secondMenuMenu = secondMenu.querySelector('.menu');
    const secondMenuToggle = secondMenu.querySelector('.header__second-menu-toggle');
    const dropdownLinks = canvas.querySelectorAll('.menu__item--expand > a');

    const ModifierClass = {
      HEADER: 'is-menu-open',
      ANIMATE: 'animate',
      SHOWN: 'shown'
    };

    let overlay = null;

    if (html.clientWidth < breakpoint) {
      openBtn.onclick = openMenu;
      //secondMenuToggle.onclick = null;

      if (dropdownLinks.length) {
        changeStateSubmenuHandler(true);
      }
    }

    if (html.clientWidth >= breakpoint && html.clientWidth < secondBreakpoint) {
      secondMenuToggle.onclick = showSecondMenu;
    }

    //appendProfile();

    function openMenu() {
      blockScroll();
      overlay = setOverlay(closeMenu);
      header.appendChild(overlay);
      canvas.classList.add(ModifierClass.ANIMATE);
      canvas.classList.add(ModifierClass.SHOWN);

      canvas.ontransitionend = () => {
        canvas.ontransitionend = null;
        canvas.classList.remove(ModifierClass.ANIMATE);
        header.classList.add(ModifierClass.HEADER);

        closeBtn.onclick = closeMenu;
        openBtn.onclick = null;
      }
    }

    function closeMenu() {
      hideSubmenu();

      header.classList.remove(ModifierClass.HEADER);
      canvas.classList.add(ModifierClass.ANIMATE);
      canvas.classList.remove(ModifierClass.SHOWN);
      
      canvas.ontransitionend = () => {
        canvas.ontransitionend = null;
        canvas.classList.remove(ModifierClass.ANIMATE);

        overlay.remove();
        overlay = null;

        closeBtn.onclick = null;
        openBtn.onclick = openMenu;
        unblockScroll();
      }
    }

    function resetMenu() {
      header.classList.remove(ModifierClass.HEADER);
      canvas.classList.remove(ModifierClass.SHOWN);

      openBtn.onclick = null;
      closeBtn.onclick = null;

      if (overlay) {
        overlay.remove();
        overlay = null;
      }
    
      unblockScroll();
    }

    function showSecondMenu() {
      secondMenu.classList.add(ModifierClass.ANIMATE);
      secondMenu.classList.add(ModifierClass.SHOWN);

      secondMenuMenu.ontransitionend = () => {
        secondMenu.classList.remove(ModifierClass.ANIMATE);
        this.onclick = hideSecondMenu;
      };
    }

    function hideSecondMenu() {
      secondMenu.classList.add(ModifierClass.ANIMATE);
      secondMenu.classList.remove(ModifierClass.SHOWN);

      secondMenuMenu.ontransitionend = () => {
        secondMenu.classList.remove(ModifierClass.ANIMATE);
        this.onclick = showSecondMenu;
      };
    }

    function resetSecondMenu() {
      secondMenu.classList.remove(ModifierClass.SHOWN);
      secondMenuToggle.onclick = null;
    }

    function showSubmenu(e) {
      e.preventDefault();
      const submenu = this.nextElementSibling;

      header.classList.remove(ModifierClass.HEADER);
      submenu.classList.add(ModifierClass.ANIMATE);
      submenu.classList.add(ModifierClass.SHOWN);

      submenu.ontransitionend = () => {
        submenu.ontransitionend = null;
        submenu.classList.remove(ModifierClass.ANIMATE);
        submenu.querySelector('.menu__back').onclick = hideSubmenu;
      }
    }

    function hideSubmenu() {
      const submenu = canvas.querySelector('.menu__submenu.shown');

      if (submenu) {
        const back = submenu.querySelector('.menu__back');

        back.onclick = null;
        
        submenu.classList.add(ModifierClass.ANIMATE);
        submenu.classList.remove(ModifierClass.SHOWN);

        submenu.ontransitionend = () => {
          submenu.ontransitionend = null;
          header.classList.add(ModifierClass.HEADER);
          submenu.classList.remove(ModifierClass.ANIMATE);
        }
      }

    }

    function resetSubmenu() {
      dropdownLinks.forEach(it => {
        it.nextElementSibling.classList.remove(ModifierClass.SHOWN);
        it.nextElementSibling.querySelector('.menu__back').onclick = null;
      });
    }

    function changeStateSubmenuHandler(addIt) {
      dropdownLinks.forEach(it => {
        it.onclick = addIt ? showSubmenu : null;
      });
    }

    function setOverlay(cb) {
      const overlay = document.createElement('div');
      overlay.classList.add('header__overlay');
      overlay.addEventListener('click', cb);
      return overlay;
    }

    function blockScroll() {
      document.body.style.overflow = 'hidden';
      document.body.style.marginRight = getScrollbarWidth() + 'px';
    }

    function unblockScroll() {
      document.body.style.overflow = '';
      document.body.style.marginRight = '';
    }

    function appendProfile() {
      if (header.classList.contains('header--homepage')) {
        if (html.clientWidth >= breakpoint) {
          canvas.appendChild(profile);
        } else {
          header.querySelector('.header__container').appendChild(profile);
        }
        
      }
    }

    window.addEventListener('resize', () => {
      if (html.clientWidth < breakpoint) {
        openBtn.onclick = openMenu;
          
        if (dropdownLinks.length) {
          changeStateSubmenuHandler(true);
        }
      } else {
        resetMenu();
        resetSubmenu();

        if (dropdownLinks.length) {
          changeStateSubmenuHandler(false);
        }
      }

      if (html.clientWidth >= breakpoint && html.clientWidth < secondBreakpoint) {
        secondMenuToggle.onclick = secondMenu.classList.contains('shown') ? hideSecondMenu : showSecondMenu;
      } else {
        resetSecondMenu();
      }
      //appendProfile();
    });
  }


  if (submenuLists.length) {
    
    if (html.clientWidth < breakpoint && !isSubmenuSimplebar) {
      submenuLists.forEach(it => {
        initSimplebar(it);
      });
      
      isSubmenuSimplebar = true;
    }

    window.addEventListener('resize', () => {
      if (html.clientWidth < breakpoint && !isSubmenuSimplebar) {
        submenuLists.forEach(it => {
          initSimplebar(it);
        });
        isSubmenuSimplebar = true;
      } else if (html.clientWidth >= breakpoint) {
        submenuLists.forEach(it => {
          destroySimplebar(it);
        });
        isSubmenuSimplebar = false;
      }
    });

    function initSimplebar(el) {
      new SimpleBar(el, { autoHide: false });
    }

    function destroySimplebar(el) {
      const items = el.querySelectorAll('.menu__submenu-item');
      el.removeAttribute('data-simplebar');
      el.innerHTML = '';

      items.forEach(it => el.appendChild(it));
    }
  }



  function getScrollbarWidth() {
    const block = document.createElement('div');
    block.style.width = '50px';
    block.style.height = '50px';
    block.style.overflow = 'auto';

    const innerBlock = document.createElement('div');
    innerBlock.style.height = '200px';

    block.appendChild(innerBlock);
    document.body.appendChild(block);

    const width = block.offsetWidth - block.clientWidth;

    block.remove();
    return width;
  }

});
$(window).on('load', function() {
	var header = $('.header:not(.header--homepage)');

	if (header.length) {
		var headerOffset = header.offset().top;
		var headerHeight = header.outerHeight();
	}

	var classes = 'header--fixed';
	var scroll = $(window).scrollTop();
	var isScroll = false;
	var isNotStatic = header.hasClass('header--half') && $(window).width() >= mobileBreakpoint ? true : false;

	$(window).on('scroll', function() {
		scroll = $(window).scrollTop();

		if (scroll >= headerOffset + headerHeight) {
			isScroll = true;

			headerHeight = isScroll ? header.outerHeight() : null;
			header.addClass(classes);

			if (!header.hasClass('is-fixed')) {
				header.css({'top': -headerHeight + 'px', 'transform': ' translateY(' + headerHeight + 'px)'}).addClass('is-fixed');

				if (!isNotStatic) {
					$('body').css('padding-top', headerHeight + 'px');
				}
			}
		} else {
			isScroll = false;
			header.removeClass(classes + ' is-fixed').removeAttr('style');
			$('body').css('padding-top', 0);

			if (!isNotStatic) {
				//body.css('padding-top', 0);
			}
		}
	});

	$(window).on('resize', function() {
		headerHeight = header.outerHeight();
		isNotStatic = header.hasClass('header--half') && $(window).width() >= mobileBreakpoint ? true : false;

		if (scroll >= headerOffset + headerHeight && isScroll) {
			header.css({'top': -headerHeight + 'px', 'transform': 'translateY(' + headerHeight + 'px)'});
			//body.css('padding-top', headerHeight + 'px');

			if (!isNotStatic) {
				$('body').css('padding-top', headerHeight + 'px');
			}
		}
	});
});
window.addEventListener('load', () => {
  const html = document.documentElement;
  const menuSubtitles = document.querySelectorAll('.footer__menu-subtitle');
  const menuTitles = document.querySelectorAll('.footer__menu-title');
  const breakpoint = 480;
  const modifierClass = 'is-open';
  const duration = 300;
  let timeoutId = null;

  if (menuSubtitles.length) {
    if (html.clientWidth <= breakpoint) {
      menuSubtitles.forEach(it => {
        it.onclick = open;
      })
    }
  }
  
  if (menuTitles.length) {
    if (html.clientWidth <= breakpoint) {
      menuTitles.forEach(it => {
        it.onclick = open;
      })
    }
  }

  window.addEventListener('resize', () => {
    if (menuTitles.length) {
      changeStateHandler(menuTitles);
    }
    if (menuSubtitles.length) {
      changeStateHandler(menuSubtitles);
    }
  })

  function changeStateHandler(arr) {
    arr.forEach(it => {
      if (html.clientWidth <= breakpoint) {
        it.onclick = it.classList.contains(modifierClass) ? close : open;
      } else {
        it.onclick = null;
        it.classList.remove(modifierClass);
      }
    });
  }

  function open(e) {
    e.preventDefault();

    const next = this.nextElementSibling;
    this.classList.add(modifierClass);
    gsap.from(next, {height: 0, duration: (duration / 1000)});
    gsap.to(this, {'margin-bottom': '20px', duration: (duration / 1000)});

    setTimeout(() => {
      next.removeAttribute('style');
      this.removeAttribute('style');
      this.onclick = close;
    }, duration + 50);

  }

  function close(e) {
    e.preventDefault();

    const next = this.nextElementSibling;
    gsap.to(next, {height: 0, duration: (duration / 1000)});
    gsap.to(this, {'margin-bottom': 0, duration: (duration / 1000)});

    setTimeout(() => {
      this.classList.remove(modifierClass);
      next.removeAttribute('style');
      this.removeAttribute('style');
      this.onclick = open;
    }, duration + 50);
 
  }

  function debounce(func, wait, immediate) {
    let timeout;

    return function executedFunction() {
      const context = this;
      const args = arguments;

      const later = function() {
        timeout = null;
        if (!immediate) func.apply(context, args);
      };

      const callNow = immediate && !timeout;

      clearTimeout(timeout);

      timeout = setTimeout(later, wait);

      if (callNow) func.apply(context, args);
    };
  };
});
